<template>
    <div class="earlyWarning">
        <div class="search">
            <el-cascader filterable size="small" v-model="search.positionId" :options="positionData" :props="{ checkStrictly: true,value:'id',label:'title',emitPath:false, }" clearable></el-cascader>
            <el-select clearable size="small" placeholder="请选择事件" v-model="search.warnType">
                <el-option v-for="item in warnTypeData" :label="item.dictionary_name" :value="item.dictionary_value" :key="item.dictionary_value"></el-option>
            </el-select>
            <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
        </div>
        <div class="home_table">
            <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                <template v-for="(item, index) in tableHead">
                    <el-table-column :prop="item.prop" v-if="item.prop=='update_time'" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                        <template slot-scope="scope">
                            {{scope.row.status==1?scope.row.update_time:''}}
                        </template>
                    </el-table-column>
                    <el-table-column :prop="item.prop" v-else align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                </template>
                <el-table-column prop="address" label="操作" fixed="right" width="100">
                    <template slot-scope="scope">
                        <el-button @click="check(scope.row)" type="text" size="small">查看</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <el-dialog title="查看预警" :visible.sync="dialogVisible" v-dialogDrag width="700px" :close-on-click-modal="false">
            <el-descriptions :column='1'>
                <el-descriptions-item label="预警来源">{{formData.device_name}}</el-descriptions-item>
                <el-descriptions-item label="预警位置">{{formData.position_name}}</el-descriptions-item>
                <el-descriptions-item label="时间">{{formData.create_time}}</el-descriptions-item>
                <el-descriptions-item label="事件">{{formData.warn_type_txt}}</el-descriptions-item>
                <el-descriptions-item label="预警处理人">{{formData.user_name}}</el-descriptions-item>
                <el-descriptions-item label="处理时间">{{formData.status==1?formData.update_time:''}}</el-descriptions-item>
                <el-descriptions-item label="预警文件">
                    <el-image v-if="formData.image_url" style="width: 100px; height: 100px;margin-right:5px;" :src="formData.image_url" :preview-src-list="[formData.image_url]">
                    </el-image>
                </el-descriptions-item>
                <el-descriptions-item label="预警情况">{{formData.case_type_txt}}</el-descriptions-item>
                <el-descriptions-item label="预警汇报">{{formData.remark}}</el-descriptions-item>
            </el-descriptions>
        </el-dialog>
    </div>
</template>

<script>
import {
    earlyWarningList,
    earlyWarningInfo,
    earlyWarningView,
} from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            tableHeight: window.innerHeight - 180,
            dialogVisible: false,
            title: '',//新增编辑标题
            search: {
                warnType: '',
                positionId: '',
            },
            formData: {
                video: {},
                image: {},
            },
            equipment: [],
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '120', },
                { title: '预警来源', prop: 'device_name', minWidth: '150', },
                { title: '预警位置', prop: 'position_name', minWidth: '220', },
                { title: '时间', prop: 'create_time', minWidth: '180', },
                { title: '事件', prop: 'warn_type_txt', minWidth: '200', },
                { title: '预警处理人', prop: 'user_name', minWidth: '180', },
                { title: '处理时间', prop: 'update_time', minWidth: '200', },
                { title: '预警情况', prop: 'case_type_txt', minWidth: '100', },
                { title: '处理结果', prop: 'status_txt', minWidth: '100', },
            ],
            tableData: [],
            positionData: [],
            warnTypeData: [],
            baseUrl: '',
        };
    },
    mounted() {
        this.baseUrl = process.env.UPLOADER_URL;
        this.getList();
        this.resView();
    },
    methods: {
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },
        async getList() {
            let data = {
                page: this.page,
                pageSize: this.pageSize,
                positionId: this.search.positionId,
                warnType: this.search.warnType,
            }
            const res = await this.axios.post(earlyWarningList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.list.data;
                this.total = res.data.data.list.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async resView() {
            const res = await this.axios.post(earlyWarningView);
            if (res.data.code == 1) {
                this.positionData = res.data.data.positionData;
                this.warnTypeData = res.data.data.warnTypeData;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        check(row) {
            this.dialogVisible = true;
            this.getInfo(row.id)
        },
        async getInfo(id) {
            const res = await this.axios.post(earlyWarningInfo, { id: id });
            if (res.data.code == 1) {
                this.formData = res.data.data.info;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        videoClick(e) {
            window.open(e, '_blank')
        },
    },
};
</script>

<style lang="scss" scoped>
.search {
    display: flex;
    padding: 12px 25px;
    background: #fff;
    .el-select {
        margin-right: 24px;
    }
    .el-input {
        width: auto;
        margin-right: 24px;
    }
    .el-cascader {
        margin-right: 24px;
    }
    .add {
        flex: 1;
        .btn_add {
            float: right;
        }
        .el-input {
            width: 500px;
        }
    }
}
</style>


